@font-face {
  font-family: "Cuprum";
  src: url("../fonts/CuprumRegular.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Cuprum";
  src: url("../fonts/CuprumBold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Cuprum";
  src: url("../fonts/CuprumItalic.woff2") format("woff2");
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/PTSans.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/PTSansBold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Cuprum", sans-serif;
  font-weight: 400;
  font-style: normal;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.hg {
  display: grid;
  grid-template-areas: "header" "main" "footer";
}
header {
  grid-area: header;
  padding-bottom: 60px;
  background-color: #F1F5F7;
}
main {
  grid-area: main;
  background-color: #FFFFFF;
}
footer {
  grid-area: footer;
  background-color: #32425C;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: 980px;
  margin: 0 auto;
  padding: 0 20px;
}
.button {
  height: 38px;
  width: 192px;
  font-family: "Cuprum", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
  background-color: #EE3643;
  text-transform: uppercase;
  text-align: center;
}
.title {
  margin: 0;
  font-size: 30px;
  line-height: 30px;
  font-weight: 400;
  color: #32425C;
  text-transform: uppercase;
}
.modal {
  display: none;
}
@keyframes splide-loading {
  0% {
    transform: rotate(0);
  }
  to {
    transform: rotate(1turn);
  }
}
.splide__container {
  position: relative;
  box-sizing: border-box;
}
.splide__list {
  margin: 0 !important;
  padding: 0 !important;
  width: -webkit-max-content;
  width: max-content;
  will-change: transform;
}
.splide.is-active .splide__list {
  display: flex;
}
.splide__pagination {
  display: inline-flex;
  align-items: center;
  width: 95%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
}
.splide__pagination li {
  list-style-type: none;
  display: inline-block;
  line-height: 1;
  margin: 0;
}
.splide {
  visibility: hidden;
}
.splide,
.splide__slide {
  position: relative;
  outline: none;
}
.splide__slide {
  box-sizing: border-box;
  list-style-type: none !important;
  margin: 0;
  flex-shrink: 0;
}
.splide__slide img {
  vertical-align: bottom;
}
.splide__slider {
  position: relative;
}
.splide__spinner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #999;
  border-left-color: transparent;
  animation: splide-loading 1s linear infinite;
}
.splide__track {
  position: relative;
  z-index: 0;
  overflow: hidden;
}
.splide--draggable > .splide__track > .splide__list > .splide__slide {
  -webkit-user-select: none;
  user-select: none;
}
.splide--fade > .splide__track > .splide__list {
  display: block;
}
.splide--fade > .splide__track > .splide__list > .splide__slide {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
}
.splide--fade > .splide__track > .splide__list > .splide__slide.is-active {
  position: relative;
  z-index: 1;
  opacity: 1;
}
.splide--rtl {
  direction: rtl;
}
.splide--ttb > .splide__track > .splide__list {
  display: block;
}
.splide--ttb > .splide__pagination {
  width: auto;
}
.action {
  display: none;
  width: 218px;
  height: 170px;
  margin: 0;
  padding: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
  background-color: #FFFFFF;
}
.action__item {
  margin-bottom: 10px;
}
.action__item:last-child {
  margin-bottom: 0;
}
.action__button-buy,
.action__button-buy-bookmark {
  width: 135px;
  height: 38px;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}
.action__button-buy {
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: #63A63E;
  box-shadow: inset 0px -3px 0px #367315;
}
.action__button-buy svg {
  margin-right: 5px;
  opacity: 0.3;
}
.action__button-buy-bookmark {
  background-color: #FFFFFF;
  color: #32425C;
  border: 3px solid #63A63E;
}
.advantage {
  margin-bottom: 60px;
}
.advantage__list {
  display: grid;
  margin: 0;
  padding: 0;
  grid-template-columns: [slider-column-start] 1fr 1fr [slider-column-end] 1fr;
  grid-template-rows: 1fr [slider-row-start] 1fr 1fr [slider-row-end];
  gap: 20px;
  list-style: none;
}
.advantage__item {
  display: flex;
  min-height: 123px;
  padding: 20px;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  top: 0;
  right: 0;
}
.advantage__item:nth-child(1) {
  background-color: #FFBF47;
}
.advantage__item:nth-child(2) {
  background-color: #3BBCE3;
}
.advantage__item:nth-child(3) {
  background-color: #DC91D8;
}
.advantage__item:nth-child(4) {
  background-color: #8ED78F;
}
.advantage__item:nth-child(5) {
  background-color: #FFBF47;
}
.advantage__item-title {
  margin: 0;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: #FFFFFF;
}
.advantage__item-label {
  width: 70px;
  height: 70px;
  padding-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  color: #FFFFFF;
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(45deg);
}
.advantage__item-label::before {
  content: "";
  width: 70px;
  height: 70px;
  background: #ffffff;
  background: linear-gradient(-45deg, rgba(255, 255, 255, 0) 60%, #ee3643 60%, #ee3643 90%, rgba(255, 255, 255, 0) 90%);
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(45deg);
  z-index: -1;
}
.advantage__item-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.advantage__button {
  min-width: 135px;
  width: auto;
  display: inline-block;
  padding-top: 10px;
  padding-left: 22px;
  padding-right: 22px;
  padding-bottom: 10px;
  background-color: rgba(255, 255, 255, 0.3);
}
.advantage__item-svg {
  align-self: center;
  fill: #FFFFFF;
  margin-right: 20px;
}
.advantage__item-svg-landscape {
  margin-right: 10px;
}
.advantage__item-slider {
  grid-column: slider-column-start / slider-column-end;
  grid-row: slider-row-start / slider-row-end;
  padding: 0;
}
.authorization {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  list-style: none;
  font-size: 21px;
  line-height: 21px;
}
.authorization li {
  margin-right: 10px;
}
.authorization li:last-child {
  margin-right: 0;
}
.authorization__link {
  display: flex;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  padding-right: 27px;
  flex-direction: row;
  align-items: center;
  color: #000000;
  background-color: #FFFFFF;
}
.authorization__auth-link svg {
  width: 20px;
  height: 17px;
  margin-right: 6px;
}
.authorization__reg-link {
  padding-left: 24px;
  padding-right: 24px;
}
.header__top {
  margin-bottom: 46px;
  background-color: #293449;
}
.header__top-box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}
.header__logo {
  width: 220px;
  height: 42px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #EE3643;
  position: relative;
}
.header__logo::before {
  content: "";
  width: 100%;
  height: 5px;
  background-color: #EE3643;
  box-shadow: inset 0px -5px 0px rgba(0, 0, 0, 0.24);
  position: absolute;
  bottom: -5px;
}
.header__bookmark,
.header__basket,
.header__order {
  display: flex;
  height: 42px;
  padding-left: 20px;
  padding-right: 20px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
}
.header__bookmark span,
.header__basket span,
.header__order span {
  padding-left: 9px;
}
.header__bookmark svg {
  width: 14px;
  height: 16px;
}
.header__basket svg {
  width: 15px;
  height: 15px;
}
.header__order {
  background-color: #63A63E;
}
.header__middle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header__title {
  margin: 0;
  width: 260px;
  padding: 30px;
  padding-top: 0;
  font-size: 16px;
  line-height: 23px;
  font-style: italic;
  color: #EE3643;
}
.header__address-phone {
  display: flex;
  width: 270px;
  margin-bottom: 3px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 35px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #FFFFFF;
  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  font-style: normal;
  color: #EE3643;
}
.header__address-phone svg {
  display: block;
  width: 19px;
  height: 19px;
  margin-right: 16px;
}
.header__address-info {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  font-style: normal;
}
.navigation {
  font-size: 16px;
}
.navigation__list {
  display: flex;
  margin: 0;
  padding: 0;
  flex-direction: row;
  justify-content: flex-end;
  list-style: none;
  background-color: #32425C;
  box-shadow: inset 0px -5px 0px #293449;
}
.navigation__list li {
  width: 100%;
  margin-right: 10px;
}
.navigation__list li:last-child {
  margin-right: 0;
}
.navigation__list a {
  display: inline-block;
  padding-left: 30px;
  padding-right: 25px;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #FFFFFF;
}
.producers {
  margin-bottom: 70px;
}
.producers__title-box {
  display: flex;
  margin-bottom: 20px;
  padding: 25px;
  padding-right: 14px;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  background-color: #F9F5F0;
}
.producers__button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.producers__list {
  display: grid;
  margin: 0;
  padding: 0;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  list-style: none;
}
.producers__link {
  display: flex;
  width: 220px;
  height: 145px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
}
.producers__link:hover,
.producers__link:focus {
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.producers__img {
  display: block;
}
.product {
  width: 220px;
  height: 318px;
  border: 1px solid #EAEAEA;
  background: #FFFFFF;
  position: relative;
}
.product:hover {
  filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.15));
}
.product__link {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.product__label {
  width: 70px;
  height: 70px;
  padding-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  color: #FFFFFF;
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(45deg);
  z-index: 1;
}
.product__label::before {
  content: "";
  width: 70px;
  height: 70px;
  background: #ee3643;
  background: linear-gradient(-45deg, rgba(255, 255, 255, 0) 60%, #ee3643 61%, #ee3643 91%, rgba(255, 255, 255, 0) 90%);
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(45deg);
  z-index: -1;
}
.product__img {
  display: block;
  margin-bottom: 8px;
  width: 218px;
  height: 170px;
  order: 1;
}
.product__title {
  display: inline-block;
  margin: 0;
  margin-left: 18px;
  margin-right: 18px;
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 20px;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  color: #000000;
  order: 2;
  text-align: center;
}
.product__item-price-old {
  margin-bottom: 13px;
  font-size: 17px;
  line-height: 18px;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  color: #A9A9A9;
  text-transform: uppercase;
  order: 3;
}
.product__item-price {
  height: 38px;
  width: 141px;
  margin-bottom: 20px;
  font-size: 17px;
  line-height: 18px;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  color: #FFFFFF;
  text-transform: uppercase;
  position: relative;
  order: 4;
}
.product__item-price span {
  margin: auto;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 45%;
  transform: translate(-50%, -50%);
}
.product:hover .product__actions {
  display: flex;
  position: absolute;
  top: 0;
}
.products {
  margin-bottom: 70px;
}
.products__title-box {
  display: flex;
  margin-bottom: 20px;
  padding: 25px;
  padding-right: 14px;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  background-color: #F9F5F0;
}
.products__button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.products__list {
  display: flex;
  margin: 0;
  padding: 0;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  list-style: none;
}
.products__item {
  margin-right: 20px;
}
.products__item:last-child {
  margin-right: 0;
}
.search {
  display: flex;
  flex-direction: row-reverse;
  margin: 0;
  padding: 0;
}
.search__button {
  display: flex;
  width: 42px;
  height: 42px;
  padding: 0;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #293449;
  border: none;
}
.search__button svg {
  width: 17px;
  height: 17px;
}
.search__input {
  padding: 0;
  padding-left: 10px;
  padding-right: 10px;
  border: none;
  background-color: #293449;
  color: #FFFFFF;
  font-family: "Cuprum", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 18px;
  appearance: none;
}
.search__input::placeholder {
  font-family: "Cuprum", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: #293449;
  color: #FFFFFF;
}
.services {
  padding-top: 65px;
  background-color: #F1F5F7;
}
.services__title-box {
  margin-bottom: 70px;
}
.services__title {
  margin-bottom: 25px;
}
.services__text {
  width: 370px;
  margin: 0;
  font-size: 13px;
  line-height: 24px;
  color: #000000;
}
.services__info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.services__nav-list {
  width: 40%;
  margin: 0;
  margin-bottom: 100px;
  padding: 0;
  list-style: none;
  position: relative;
}
.services__nav-list::after {
  content: "";
  width: 10px;
  height: 279px;
  background-image: url("../img/Shadow.png");
  position: absolute;
  right: 0;
  top: -30%;
}
.services__nav-button {
  display: flex;
  width: 100%;
  padding-left: 22px;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-right: 22px;
  border: none;
  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  color: #FFFFFF;
  background: #32425C;
  box-shadow: 0px 1px 0px #293449, inset 0px 1px 0px #405069;
}
.services__nav-button:hover {
  cursor: pointer;
}
.services__nav-button--current {
  color: #32425C;
  background: #FFFFFF;
  box-shadow: 0px 1px 0px #FFFFFF;
}
.services__nav-button--current:hover {
  cursor: default;
}
.services__list {
  width: 60%;
  margin: 0;
  padding: 0;
  padding-left: 80px;
  width: 100%;
  list-style: none;
}
.services__item {
  display: none;
  min-height: 283px;
  background-position: right;
  background-repeat: no-repeat;
}
.services__item--current {
  display: block;
}
.services__item .services__text {
  margin-bottom: 0;
}
.services__button {
  border: none;
  border-radius: 2px;
  background-color: #FF5357;
}
.services__item--car {
  background-image: url("../img/Car.png");
}
.services__item--many {
  background-image: url("../img/Many.png");
}
.services__item--medal {
  background-image: url("../img/Medal.png");
}
.slider {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;
}
.slider__content {
  width: 100%;
  height: 100%;
  position: relative;
}
.slider__list {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.slider__item {
  width: 100%;
  height: 100%;
  position: relative;
}
.slider__item--hidden {
  display: none;
}
.slider__item-img {
  display: block;
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
}
.slider__item-content {
  height: 100%;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.slider__item-description {
  position: relative;
}
.slider__item-title {
  margin: 0;
  margin-bottom: 5px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 700;
  color: #FFFFFF;
}
.slider__item-text {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  color: #FFFFFF;
}
.slider__button {
  display: inline-block;
  padding-top: 10px;
  padding-left: 46px;
  padding-right: 46px;
  padding-bottom: 10px;
  background-color: #EE3643;
  position: relative;
}
.slider__nav-prev {
  padding: 10px;
  border: none;
  background-color: inherit;
  position: absolute;
  top: 50%;
  left: 35px;
  transform: translate(-50%, -50%) rotate(180deg);
  z-index: 1;
}
.slider__nav-prev:hover {
  cursor: pointer;
  opacity: 0.6;
}
.slider__nav-next {
  padding: 10px;
  border: none;
  background-color: inherit;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.slider__nav-next:hover {
  cursor: pointer;
}
.slider__nav-arrows svg {
  width: 30px;
  fill: #FFFFFF;
}
.slider__marker-list {
  display: flex;
  margin: auto;
  padding: 0;
  list-style: none;
  flex-direction: row;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slider__marker-list li {
  margin-right: 10px;
}
.slider__marker-list li:last-child {
  margin-right: 0;
}
.slider__marker-item {
  display: block;
  width: 10px;
  height: 10px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background: #FFFFFF;
  position: relative;
}
.slider__marker-item:hover {
  cursor: pointer;
}
.slider__marker-item.is-active:hover {
  cursor: default;
}
.slider__marker-item.is-active::after {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #EE3643;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
